* {
  box-sizing: border-box;
  font-size: 15px;

}
body{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

a {
  color: #333;
  text-decoration: none;
}
.road {
  background-image: url(img/road.jpg);
  background-attachment: fixed;
  padding-bottom: 60px;
   padding-top: 60px;
  font-size: 27px;
  margin-top: 20px;
  width: 100%;
}
img {
  max-width: 100%;
}
.line {
  width: 23px;
  height: 2px;
  background-color: white;
  margin: 5px;
}
.hamburger {
  position: fixed;
  cursor: pointer;
  right: 1%;
  top: 7%;
  transform: translate(-60%, -80%);
  background-color: black;
  padding: 6px;
  border-radius: 5px;
}
.flower {
  text-align: center;
  width: 100%;
  margin: 0 auto;
  max-width: 900px;
}
.contain {
  background-color: rgba(250, 250, 250, 0.5);
  margin-top: 60px;
  padding-bottom: 30px;
  margin-bottom: 20px;
}
.nav-link  {
  background: #383e4c;
  width: 80%; 
  position: fixed; 
  font-size: 10px;
  font-weight: 100px;
  top: 10%;
  left: 13%;   
}
 #hidden {
  display: none;
}
.nav-link  a {
  display: block;
  color: white;
  padding-top: 15px;
  padding-bottom: 5px;
  font-size: 15px;
  font-weight: bold;
  border-bottom: solid rgba(255, 255, 255, 0.1);
  text-align: center;  
  width: 100%;
  text-decoration: none;
} 
li {
  list-style: none;
  /* padding: 5px; */
}
ul {
  padding: 0px;
}
.nav-link a:hover {
  background-color: #49505f;
  list-style: none; 

}
.round {
  margin: 0;
}
.hero-img {
  background-image: url(./img/tik.jpg);
  background-repeat: no-repeat;  
  background-size: 100% 100%;
  background-position: cover;
  background-attachment: fixed;
  height: 300px;
  padding:10%;
  margin-bottom: 70px;
  color: white;
  font-size: 20px;
  text-align: center;
} 
.top {
  text-align: center; 
  width: 100%;     
}
.middle {
  background-image: url(img/bg-2.jpg);
  background-position: center center;
  background-repeat: no-repeat; 
  background-size: cover;
  background-attachment: fixed;
  height: 350px;
  text-align: center;
  color: white;
  padding: 35px;
  font-size: 20px;
  margin-top: 20px;

} 
h2 {
  margin-top: 20px;
  color: green;
}
h1 {
    color: white;
}
.container {
  width: 90%; 
  margin: 0 auto;
  background-color: rgba(250, 250, 250, 0.5);
  margin-top: 30px;
  padding-bottom: 30px;
  text-align: center;
  font-size: 16px;
  word-spacing: 2px;
  color: grey;   
}
.btn {
  background-color: green;
  padding: .5em 1.25em;
  text-decoration: none;
  color: white;
  display: inline-block;
  font-weight: 900;
  font-size: 15px;
  border-radius: 10px;
}
.blue {
  background-color: blue;
  padding: .5em 1.25em;
  text-decoration: none;
  color: white;
  display: inline-block;
  font-weight: 900;
  font-size: 15px;
  border-radius: 10px;
}
.white {
  background-color: white;
  padding: .5em 1.25em;
  text-decoration: none;
  color: black;
  display: inline-block;
  font-weight: 900;
  font-size: 15px;
  border-radius: 10px;
  border: 1px solid grey;
}
.black {
  background-color: black;
  padding: .5em 1.25em;
  text-decoration: none;
  color: white;
  display: inline-block;
  font-weight: 900;
  font-size: 15px;
  border-radius: 10px;
  border-color: white;
}
.red {
  background-color: red;
  padding: .5em 1.25em;
  text-decoration: none;
  color: white;
  display: inline-block;
  font-weight: 900;
  font-size: 15px;
  border-radius: 10px;
}
.last-btn {
  margin-left: 20px; 
  margin-top: 20px;
}
.last {
   width: 90%;
   margin: 0 auto;
   padding: 9px;
   margin-top: 19px;
   margin-bottom: 90px;
   text-align: center;
   font-size: 17px;
}
.input {
  width: 95%;
  margin: 0 auto;
  background-color: rgba(250, 250, 250, 0.5);
  padding: 19px;
  border-radius: 5px;
  margin-top: 19px;  
  max-width: 900px;
  outline: 0px;
  font-size: 15px;
}
textarea {
  width: 95%;
  margin: 0 auto;
  background-color: rgba(250, 250, 250, 0.5);
  padding: 19px;
  border-radius: 5px;
  margin-top: 19px;   
  max-width: 900px;
}
.fot {
  width: 96%;
  margin: 0 auto;
  background-color: rgba(250, 250, 250, 0.5);
  padding: 19px;
  border-radius: 5px;
  margin-top: 19px;
  text-align: center;
}
@media (min-width:1000px) {
  .hero-img {
      padding: 20%;
      font-size: 30px;
      height: 900px;
  }
  .row {
      display: flex;
      justify-content: space-around;   
      width: 90%;
      margin: 0 auto; 
      
  }
  .container, .contain {
      width: 90%;
  }
  .rot {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      width: 90%;
      margin: 0 auto;
  }
  .cut {
      display: grid;
      grid-template-columns: 2fr 1fr;
  }
   textarea {
       margin-left: 60px;
   }
  .hamburger {
      display: none;
  }
  .nav-link {
    background-color:#383e4c;  
  }
  #hidden {
    display: flex;
    left: 0%;
  
  }
  ul {
      list-style: none;
      margin: 0;
      padding: 0;
      background-color:#383e4c; 
      position: fixed;
      top: 0;
      width: 100%;
      display: flex;
      justify-content: center;
  }
  .nav-link li a {
      margin-bottom: 20px;
      margin-top: 15px; 
  }
  .nav-link li a {
      padding: 14px 16px;
      border: solid rgba(255, 255, 255, 0.1);
      font-size: 30px;
      margin-left: 10px;
      margin-top: 15px;
  }
}
